@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;1,100;1,200;1,300;1,400;1,500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600&display=swap);
.App {
    text-align: center;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

/***********************RESET******************************/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
    display: block;
}
body {
    line-height: 1;
}
ol,
ul {
    list-style: none;
}
blockquote,
q {
    quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
    content: "";
    content: none;
}
table {
    border-collapse: collapse;
    border-spacing: 0;
}

* {
    box-sizing: border-box;
}

.flex {
    display: flex;
}

.flex-column {
    display: flex;
    flex-direction: column;
}

.App {
    --light-gray: #ebebeb;
    --light-yellow: #e7cb6c;
    --dark-yellow: #ffbb00;
    --dark-aqua: #679499;
}

.link {
    text-decoration: none;
    cursor: pointer;
    transition: all 0.3s;
}

.link:hover {
    color: var(--dark-yellow);
}

.unselectable {
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
}


.footer {
    width: 100%;
    background-color: #525252;
    min-height: 16vh;
    align-items: center;
    justify-content: space-between;
    padding-left: 15%;
    padding-right: 15%;
}

.footer p {
    font-family: "Poppins", sans-serif;
    color: var(--light-gray);
    font-size: 15px;
    font-weight: 300;
    margin-bottom: 3px;
}

.footer p span {
    font-weight: 500;
}

.footer .block {
    align-items: flex-start;
}

.footer .coffee {
    align-self: center;
    justify-content: center;
    cursor: pointer;
}

.footer .coffee_icon {
    margin-left: 8px;
    cursor: pointer;
    transition: all 0.3s;
    color: #ffffff;
}

.footer .coffee_icon:hover {
    color: var(--dark-yellow);
}

.footer .icons {
    width: 100%;
    margin-bottom: 8px;
    align-items: center;
    justify-content: center;
}

.footer .icon {
    color: #ffffff;
    height: 35px;
    width: 35px;
    transition: all 0.3s;
    cursor: pointer;
}

.footer .icon:hover {
    color: #ffbb00;
}

@media screen and (min-width:769px) and (max-width:1370px){

    .footer{
        min-height: 20vh;
    }

    .footer p {
        font-size: 12px;
    }
}

@media screen and (max-width: 768px) {
    .footer {
        width: 100%;
        margin-left: auto;
        margin-right: auto;
        flex-direction: column;
        padding: 50px 30px;
    }

    .footer .block {
        margin-bottom: 30px;
        text-align: center;
        align-items: center;
    }

    .footer .block:last-child {
        margin-bottom: 0;
    }

    .footer p span {
        color: var(--dark-yellow);
    }

    .footer .icon {
        color: var(--dark-yellow);
    }
}

