    background-image: url(/joyceapp/static/media/b2.7e271ece.png);
    background-repeat: no-repeat;
    background-size: cover;
}

.home_panel .b3 {
    position: absolute;
    bottom: 40px;
    left: 40px;
    z-index: 4;
