    height: 100%;
    width: 50vw;
    background-color: #ffffff;
    opacity: 0.75;
    -webkit-clip-path: polygon(0 0, 52% 0, 100% 100%, 0 100%);
            clip-path: polygon(0 0, 52% 0, 100% 100%, 0 100%);
    box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.3);
    border-radius: 4px;
