.portifolio {
    margin-top: 50px;
    min-height: calc(100% - 50px);
    height: auto;
    width: 100%;
}

.portifolio .center_text {
    width: 100%;
    text-align: left;
    margin-bottom: 50px;
}

.portifolio .grey_block {
    background-color: #f4f4f4;
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.1);
}

.portifolio .parte1 {
    width: 80%;
    margin-left: auto;
    margin-right: auto;
    min-height: 100vh;
    height: auto;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
}

.portifolio .section {
    margin-bottom: 50px;
}

.portifolio .first_panel {
    justify-content: space-between;
}

.portifolio .parte1 .parte1_title {
    width: 100%;
    text-align: left;
    margin-bottom: 50px;
}

.portifolio .parte1 .box {
    position: relative;
    width: 96%;
    height: 400px;
    box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.4);
    border-radius: 4px;
    transition: ease-in-out 0.3s;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

.portifolio .parte1 .box img {
    opacity: 0.8;
    width: 55%;
    height: auto;
}

.portifolio .parte1 .box .title {
    bottom: 30px;
    right: 30px;
    font-size: 24px;
    position: absolute;
    color: #ffffff;
    font-weight: bold;
    font-family: "Poppins", sans-serif;
}

.portifolio .description {
    background-color: #e3e3e1;
    font-family: "Poppins", sans-serif;
    text-align: left;
    width: 450px;
    margin-top: 30px;
    padding: 20px;
    border-radius: 4px;
    margin-left: 15px;
    margin-right: 15px;
}

.blue {
    color: #e7f3f5;
}

.portifolio .parte1 .box:hover {
    transform: scale(1.01);
    box-shadow: 0px 0px 6px rgba(0, 0, 0, 0.5);
}

.portifolio .block_1 {
    background-color: #64adb7;
}
.portifolio .block_2 {
    background-color: #ff5d5d;
}
.portifolio .block_3 {
    background-color: #ffd955;
}

.portifolio .section_title {
    text-align: left;
    margin-bottom: 20px;
    font-family: "Montserrat", sans-serif;
    text-transform: uppercase;
    font-weight: 600;
    font-size: 40px;
}

.portifolio .section_description {
    text-align: left;
    margin-bottom: 40px;
    font-family: "Montserrat", sans-serif;
    font-weight: 400;
    font-size: 20px;
}

.portifolio .subsection {
    height: auto;
    /* min-height: 100vh; */
    padding-top: 14vh;
    margin-left: 10%;
    margin-right: 10%;
    padding-bottom: 100px;
    display: flex-column;
    align-items: center;
    justify-content: center;
}

.portifolio .sub_subsection {
    height: auto;
    /* min-height: 100vh; */
    padding-top: 14vh;
    margin-left: 10%;
    margin-right: 10%;
    padding-bottom: 100px;
    display: flex-column;
    align-items: center;
    justify-content: center;
    padding-top: 50px;
    padding-bottom: 50px;
}

.portifolio .subsection_title {
    text-align: left;
    text-transform: capitalize;
    font-family: "Montserrat", sans-serif;
    font-weight: 600;
    font-size: 40px;
}

.portifolio .subsection_description {
    text-align: left;
    font-family: "Poppins", sans-serif;
    font-weight: 400;
    font-size: 20px;
    margin-bottom: 40px;
}

.portifolio .projetos {
    justify-content: left;
}

.portifolio .projetos .section_options {
    width: 100%;
    flex-wrap: wrap;
    justify-content: space-between;
}

.portifolio .projetos .section_options .section_box {
    height: 40vh;
    width: 32%;
    position: relative;
    box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.4);
    border-radius: 4px;
    transition: ease-in-out 0.3s;
}
.portifolio .projetos .section_options .section_box:hover {
    transform: scale(1.01);
    box-shadow: 0px 0px 6px rgba(0, 0, 0, 0.7);
}

.portifolio .projetos .section_options .section_box .title {
    bottom: 30px;
    right: 30px;
    font-size: 24px;
    position: absolute;
    color: #ffffff;
    font-weight: bold;
    font-family: "Poppins", sans-serif;
}

.portifolio .project_galery {
    width: 100%;
    flex-wrap: wrap;
    justify-content: flex-start;
}

.portifolio .project_galery .project {
    position: relative;
    height: 250px;
    width: 31.5%;
    margin-left: 0.75%;
    margin-right: 0.75%;
    min-width: 300px;
    margin-bottom: 30px;
    box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.4);
    border-radius: 4px;
    background-color: #626397;
    overflow: hidden;
    transition: all 0.2s;
}

.portifolio .project_galery .project img {
    width: 100%;
    height: 100%;
}

.portifolio .project_galery .project:hover {
    transform: scale(1.01);
    box-shadow: 0px 0px 6px rgba(0, 0, 0, 0.7);
}

.portifolio .project_galery .project .project_name {
    bottom: 30px;
    right: 30px;
    font-size: 24px;
    position: absolute;
    color: #ffffff;
    text-align: right;
    font-weight: bold;
    font-family: "Poppins", sans-serif;
}

.portifolio .title_line {
    align-self: center;
    margin-bottom: 20px;
}

.portifolio .title_line .section_icon {
    height: 50px;
    margin-right: 20px;
}

@media screen and (min-width:769px) and (max-width:1370px){
    
    .portifolio .section_description{
        font-size: 18px;
    }
    .portifolio .subsection_description{
        font-size: 18px;
    }
    .portifolio .project_galery .project .project_name{
        font-size: 20px;
    }

    .portifolio .parte1 .box{
        height: 300px;
    }

    .portifolio .section_title{
        font-size: 36px;
    }

    .portifolio .subsection_title{
        font-size: 36px;
    }

    .portifolio .title_line .section_icon{
        height: 44px;
    }

    .portifolio .parte1 .box .title{
        font-size: 20px;
        max-width: 80%;
        text-align: right;
    }

    .portifolio .project_galery .project .project_name{
        width: 80%;
    }

    .portifolio .projetos .section_options .section_box{
        height: 32vh;
    }

    .portifolio .project_galery .project{
        height: 32vh;
        min-width: 0px;
        width: 30.5%;
    }

    .portifolio .project_galery .project img {
        object-fit: cover;
    }
}

@media screen and (max-width: 768px) {
    .portifolio {
        margin-top: 150px;
        width: 100%;
    }

    .portifolio .first_panel {
        flex-direction: column;
    }

    .portifolio .section {
        box-sizing: border-box;
        width: 100%;
    }

    .portifolio .parte1 .parte1_title {
        font-size: 36px;
    }

    .portifolio .section_title {
        font-size: 36px;
        text-align: center;
    }

    .portifolio .subsection_title {
        font-size: 36px;
    }

    .portifolio .parte1 .box img {
        width: 38%;
        height: auto;
        margin-bottom: 10%;
    }

    .portifolio .parte1 .box .title {
        text-align: right;
        width: 60%;
        font-size: 22px;
        bottom: 20px;
    }

    .portifolio .description {
        width: 100%;
    }

    .portifolio .parte1 .box {
        height: 200px;
        width: 100%;
    }

    .portifolio .projetos .section_options {
        flex-direction: column;
    }

    .portifolio .projetos .section_options .section_box {
        width: 100%;
        height: 120px;
        margin-bottom: 20px;
    }

    .portifolio .project_galery .project {
        width: 100%;
        height: 180px;
    }

    .portifolio .project_galery .project img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}


.apresentacao {
    margin-top: 60px;
    min-height: 100vh;
    width: 100%;
}

.apresentacao .scrollable {
    max-height: 70vh;
    overflow-y: scroll;
    overflow-x: hidden;
}

.apresentacao h1 {
    font-size: 70px;
    font-family: "Poppins", sans-serif;
    font-weight: 700;
    margin-top: 30px;
    margin-bottom: 50px;
}

.apresentacao .block_1 .info {
    position: absolute;
